.wrap{width:100%;height:100%;display:flex;align-items:center;justify-content:center;background-color:#ededed;transition:background-color .5s ease-in-out;-moz-transition:background-color .5s ease-in-out;-webkit-transition:background-color .5s ease-in-out;-ms-transition:background-color .5s ease-in-out;-o-transition:background-color .5s ease-in-out}.wrap .backBtn{display:flex;align-items:center;position:absolute;top:20px;left:20px}.wrap .backBtn>span{display:flex;align-items:center;cursor:pointer}.wrap .innerWrap{display:flex;flex-direction:column;align-items:center;justify-content:center;word-break:break-word;text-align:center}.wrap .innerWrap>h1{margin-bottom:0}.wrap .innerWrap>span{width:100%;height:100%}@media(max-width: 768px){.wrap .innerWrap{padding-top:100px}.wrap .innerWrap>span>svg{width:300px;height:300px}}