.background{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.4);display:flex;justify-content:center;align-items:center;z-index:200}.background .modalBody{display:flex;align-items:center;justify-content:space-around;flex-direction:column;align-items:center;position:absolute;top:50%;left:50%;width:200px;height:120px;padding:40px;text-align:center;background-color:#ededed;border-radius:10px;transform:translateX(-50%) translateY(-50%)}.background .modalBody>span{font-size:1.3em;font-weight:bold}.background .modalBody>div{width:100%;display:flex;justify-content:space-around}.background .modalBody>div>button{width:40%;height:40px;box-sizing:border-box;color:#000;font-weight:bold;background:#fff;border:none;border-radius:10px;cursor:pointer;box-shadow:"none";transition:box-shadow .5s ease-in-out;-moz-transition:box-shadow .5s ease-in-out;-webkit-transition:box-shadow .5s ease-in-out;-ms-transition:box-shadow .5s ease-in-out;-o-transition:box-shadow .5s ease-in-out}.background .modalBody>div>button:hover{box-shadow:4px 4px 2px 1px rgba(0,0,0,.2);transition:box-shadow .5s ease-in-out;-moz-transition:box-shadow .5s ease-in-out;-webkit-transition:box-shadow .5s ease-in-out;-ms-transition:box-shadow .5s ease-in-out;-o-transition:box-shadow .5s ease-in-out}.background.night .modal_body{color:#dcdcdc;background-color:#434343}.background.night .modal_body>div>button{color:#dcdcdc;background:#242424;box-shadow:"none";transition:box-shadow .5s ease-in-out;-moz-transition:box-shadow .5s ease-in-out;-webkit-transition:box-shadow .5s ease-in-out;-ms-transition:box-shadow .5s ease-in-out;-o-transition:box-shadow .5s ease-in-out}.background.night .modal_body>div>button:hover{box-shadow:4px 4px 2px 1px rgba(0,0,0,.2);transition:box-shadow .5s ease-in-out;-moz-transition:box-shadow .5s ease-in-out;-webkit-transition:box-shadow .5s ease-in-out;-ms-transition:box-shadow .5s ease-in-out;-o-transition:box-shadow .5s ease-in-out}