.wrap{width:100%;min-height:100vh;position:relative;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#ededed;transition:background-color .5s ease-in-out;-moz-transition:background-color .5s ease-in-out;-webkit-transition:background-color .5s ease-in-out;-ms-transition:background-color .5s ease-in-out;-o-transition:background-color .5s ease-in-out}.wrap .innerWrap{width:calc(100% - 40px);height:100%;padding:20px}.wrap .innerWrap>div{width:100%;padding:20px;border:solid 1px #000;box-sizing:border-box;margin-bottom:15px}.wrap .innerWrap>div .title{font-size:1.5rem;font-weight:bold;margin:0 0 10px}.wrap .innerWrap>div .subTitle{font-size:1.2rem;margin:0 0 10px}.wrap .innerWrap .inputSection{display:flex}.wrap .innerWrap .inputSection>div{width:50%}.wrap .innerWrap .inputSection .paramsField{margin-top:20px}.wrap .innerWrap .inputSection .paramsField>p{margin:0}.wrap .innerWrap .transactionSection{width:100%}.wrap .innerWrap .transactionSection .buttonBox{display:flex;align-items:center;margin-bottom:10px}.wrap .innerWrap .transactionSection .buttonBox>button{font-size:1rem;padding:5px;margin-right:15px;background-color:#dcdcdc}@media(max-width: 768px){.wrap .innerWrap .inputSection{flex-direction:column}.wrap .innerWrap .inputSection>div{width:100%}.wrap .innerWrap .transactionSection .buttonBox{flex-direction:column}.wrap .innerWrap .transactionSection .buttonBox>button{width:100%;margin-bottom:5px}}