.background{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.4);display:flex;justify-content:center;align-items:center;z-index:10000}.background .loader{width:200px;height:200px;display:inline-block;position:relative}.background .loader::after,.background .loader::before{content:"";box-sizing:border-box;width:200px;height:200px;border-radius:50%;border:2px solid #fff;position:absolute;left:0;top:0;animation:animloader 2s linear infinite}.background .loader::after{animation-delay:1s}@keyframes animloader{0%{transform:scale(0);opacity:1}100%{transform:scale(1);opacity:0}}